import {Component, OnDestroy, OnInit} from '@angular/core';
import {RolesService} from '../../../providers/roles.service';
import {ModulesService} from '../../../providers/modules.service';
import {ActivatedRoute} from '@angular/router';
import {SweetAlertService} from 'angular-sweetalert-service';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';
import {DataTableResource} from '../../../shared/components/data-table';


@Component({
  selector: 'app-edit-roles-permissions',
  templateUrl: './edit-roles-permissions.component.html'
})
export class EditRolesPermissionsComponent implements OnInit, OnDestroy {

  modules: any;
  permissions: any;
  role: any;
  private sub: any;

  itemResource: any;
  rows = [];
  itemCount = 0;
  loaded: boolean;
  defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Roles por página',
    paginationRange: 'Total de roles'
  };

  constructor(private rolesService: RolesService,
              private modulesService: ModulesService, private route: ActivatedRoute, private alertService: SweetAlertService) {
    this.modules = [];
    this.permissions = [];
    this.role = {};
    this.loaded = false;
  }

  reloadItems(params) {
    if (this.itemResource) {
      this.itemResource.query(params).then(rows => this.rows = rows);
    }
  }

  get(id: string) {

    if (id) {
      this.rolesService.getById(id).subscribe(res => {
        if (res.StatusCode === 200) {
          this.role = res.Result;
          this.getModulesByRol(id);
        } else {
          this.alertService.warning({
            title: res.error_description || 'No fue posible obtener los datos del rol'
          });
        }
      });
    } else {
      this.alertService.warning({
        title: 'No se encontro el un ID para el rol'
      });
    }

  }

  getModules() {
    this.modulesService.get().subscribe(res => {
      if (res.StatusCode === 200) {
        this.loaded = true;

        let mods = res.Result;

        mods = mods.map((module) => {

          return {
            selected: this.permissions.filter((per) => {
              return per.MO_ID_MODULO === module.MO_ID_MODULO;
            }).length > 0,
            MO_ID_MODULO: module.MO_ID_MODULO,
            MO_MODULO: module.MO_MODULO,
            MO_PATH: module.MO_PATH,
            MO_DESCRIPCION: module.MO_DESCRIPCION,
            MO_ESTATUS: module.MO_ESTATUS,
            MO_ESTATUS_DESCRIPCION: module.MO_ESTATUS_DESCRIPCION,
            MO_FECHA_CREACION: module.MO_FECHA_CREACION,
            MO_USUARIO_CREACION: module.MO_USUARIO_CREACION,
            MO_FECHA_MOD: module.MO_FECHA_MOD,
            MO_USUARIO_MOD: module.MO_USUARIO_MOD,
            MO_ULTIMO_ACCESO: module.MO_ULTIMO_ACCESO
          };
        });

        this.itemResource = new DataTableResource(mods);
        this.itemResource.count().then(count => this.itemCount = count);
        this.reloadItems({});
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible obtener la lista de roles disponibles en el sistema'
        });
      }
    });
  }

  getModulesByRol(id: string) {
    this.modulesService.getByRol(id).subscribe(res => {
      if (res.StatusCode === 200) {
        this.permissions = res.Result;
        this.getModules();
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible obtener la lista permisos para este rol'
        });
      }
    });
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.get(params['id']);

    });
  }

  saveModules() {
    this.itemResource.query({}).then(rows => {
      let modules = rows.map((item) => {

        if (item.selected) {
          return {
            'MO_ID_MODULO': item.MO_ID_MODULO
          };
        }
      });

      modules = modules.filter((mod) => {
        if (mod) {
          return true;
        } else {
          return false;
        }
      });

      this.rolesService.saveModules({EnrolledModulos: modules, Id: this.role.Id}).subscribe(res => {
        if (res.StatusCode === 200) {
          this.alertService.success({
            title: 'Rol actualizado exitosamente'
          });
        } else {
          this.alertService.warning({
            title: res.error_description || 'No fue posible actualizar los roles del usuario'
          });
        }
      });
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}



