import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {RolesComponent} from './list-roles.component';
import {AddRolesComponent} from './add-roles.component';
import {EditRolesComponent} from './edit-roles.component';
import {EditRolesPermissionsComponent} from './edit-roles-permissions.component';
import {UsersRolesPermissionsComponent} from './users-roles.component';

import {DataTableModule} from '../../../shared/components/data-table';
import {TooltipModule} from 'ngx-bootstrap/tooltip';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {ENgxPrintModule} from '../../../shared/components/e-ngx-print';

const TABLE_ROUTES: Routes = [
  {path: '', component: RolesComponent},
  {path: 'add', component: AddRolesComponent},
  {path: 'edit/:id', component: EditRolesComponent},
  {path: ':id/permissions', component: EditRolesPermissionsComponent},
  {path: ':id/users', component: UsersRolesPermissionsComponent}
];

@NgModule({
  declarations: [
    RolesComponent,
    EditRolesComponent,
    AddRolesComponent,
    EditRolesPermissionsComponent,
    UsersRolesPermissionsComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ENgxPrintModule,
    DataTableModule,
    RouterModule.forChild(TABLE_ROUTES),
    TooltipModule.forRoot()
  ]
})

export class RolesModule {
}



