import {Component, OnDestroy, OnInit} from '@angular/core';
import {RolesService} from '../../../providers/roles.service';
import {UsersService} from '../../../providers/users.service';
import {ModulesService} from '../../../providers/modules.service';
import {ActivatedRoute} from '@angular/router';
import {SweetAlertService} from 'angular-sweetalert-service';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';
import {DataTableResource} from '../../../shared/components/data-table';


@Component({
  selector: 'app-users-roles',
  templateUrl: './users-roles.component.html'
})
export class UsersRolesPermissionsComponent implements OnInit, OnDestroy {

  users: any;
  role: any;
  private sub: any;

  itemResource: any;
  rows = [];
  itemCount = 0;
  loaded: boolean;
  defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Roles por página',
    paginationRange: 'Total de roles'
  };

  constructor(private rolesService: RolesService,
              private modulesService: ModulesService,
              private usersService: UsersService,
              private route: ActivatedRoute, private alertService: SweetAlertService) {
    this.users = [];
    this.role = {};
    this.loaded = false;
  }

  reloadItems(params) {
    if (this.itemResource) {
      this.itemResource.query(params).then(rows => this.rows = rows);
    }
  }

  get(id: string) {

    if (id) {
      this.rolesService.getById(id).subscribe(res => {
        if (res.StatusCode === 200) {
          this.role = res.Result;
          this.getUsersByRol(id);
        } else {
          this.alertService.warning({
            title: res.error_description || 'No fue posible obtener los datos del rol'
          });
        }
      });
    } else {
      this.alertService.warning({
        title: 'No se encontro el un ID para el rol'
      });
    }

  }

  getUsersByRol(id: string) {
    this.usersService.getByRole(id).subscribe(res => {
      if (res.StatusCode === 200) {
        this.users = res.Result;
        this.itemResource = new DataTableResource(this.users);
        this.itemResource.count().then(count => this.itemCount = count);
        this.loaded = true;
        this.reloadItems({});
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible obtener la lista permisos para este rol'
        });
      }
    });
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.get(params['id']);

    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}



