import {Component, OnInit} from '@angular/core';
import {RolesService} from '../../../providers/roles.service';
import {DataTableResource} from '../../../shared/components/data-table';
import {DataTableTranslations} from '../../../shared/components/data-table/components/types';
import {SweetAlertService} from 'angular-sweetalert-service';

@Component({
  selector: 'app-list-roles',
  templateUrl: './list-roles.component.html'
})
export class RolesComponent implements OnInit {

  itemResource: any;
  rows = [];
  itemCount = 0;
  loaded: boolean;
  printStyle: string;
  defaultTranslations = <DataTableTranslations>{
    indexColumn: 'Indice',
    selectColumn: 'Seleccionar',
    expandColumn: 'Expandir',
    paginationLimit: 'Roles por página',
    paginationRange: 'Total de roles'
  };
  statuses: any;


  constructor(private rolesService: RolesService, private alertService: SweetAlertService) {
    this.loaded = false;
    this.statuses = {
      0: {text: 'Inactivo'},
      1: {text: 'Activo'},
      2: {text: 'Eliminado'}
    };
    this.printStyle =
      `
			@media print{@page {size: landscape}}
			html {
				color: #000 !important;
			}
			`;
  }

  reloadItems(params) {
    if (this.itemResource) {
      this.itemResource.query(params).then(rows => this.rows = rows);
    }
  }

  print() {

  }

  delete(item: any) {
    this.alertService.confirm({
      title: '¿Desea eliminar este registro?',
      confirmButtonText: 'Sí',
      cancelButtonText: 'No'
    }).then((res) => {
      if (res.value) {
        item.Estatus = 2;
        this.rolesService.update(item).subscribe(res => {
          if (res.StatusCode === 200) {
            this.alertService.success({
              title: 'Rol Eliminado exitosamente'
            });
            this.getRoles();
          } else {
            this.alertService.warning({
              title: res.error_description || 'No fue posible eliminar el rol'
            });
          }
        });
      }
    });
  }

  ngOnInit() {
    this.getRoles();
  }

  getRoles() {
    this.rolesService.get().subscribe(res => {
      if (res.Result) {
        this.loaded = true;
        this.itemResource = new DataTableResource(res.Result);
        this.itemResource.count().then(count => this.itemCount = count);
        this.reloadItems({});
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible consultar la lista de roles'
        });
      }
    });
  }
}



