import {Component, OnInit} from '@angular/core';
import {RolesService} from '../../../providers/roles.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SweetAlertService} from 'angular-sweetalert-service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-add-roles',
  templateUrl: './add-roles.component.html'
})
export class AddRolesComponent implements OnInit {

  saveForm: FormGroup;

  constructor(private rolesService: RolesService, private router: Router, private fb: FormBuilder, private alertService: SweetAlertService) {
    this.createForm();
  }

  save() {
    const formModel = this.saveForm.value;
    this.rolesService.save(formModel).subscribe(res => {
      if (res.StatusCode === 200) {
        this.alertService.success({
          title: 'Rol agregado exitosamente'
        });

        this.router.navigate(['/security/roles']);
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible Agregar el rol'
        });
      }
    });

  }

  ngOnInit() {

  }

  createForm() {
    this.saveForm = this.fb.group({
      Name: ['', Validators.required],
      Description: ['', Validators.required]
    });
  }

  get Name() {
    return this.saveForm.get('Name');
  }

  get Description() {
    return this.saveForm.get('Description');
  }
}



