import {Component, OnDestroy, OnInit} from '@angular/core';
import {RolesService} from '../../../providers/roles.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Select2OptionData} from 'ng2-select2';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SweetAlertService} from 'angular-sweetalert-service';

@Component({
  selector: 'app-edit-roles',
  templateUrl: './edit-roles.component.html'
})
export class EditRolesComponent implements OnInit, OnDestroy {

  saveForm: FormGroup;
  role: any;
  private sub: any;
  statuses: Array<any> = [
    {id: '0', text: 'Inactivo'},
    {id: '1', text: 'Activo'},
    {id: '2', text: 'Eliminado'}
  ];
  options: any = {
    dropdownAutoWidth: true,
    width: '100%',
    containerCssClass: 'select2-selection--alt',
    dropdownCssClass: 'select2-dropdown--alt',
    placeholder: 'Seleccione una opción'
  };

  constructor(private rolesService: RolesService, private router: Router,
              private fb: FormBuilder, private route: ActivatedRoute, private alertService: SweetAlertService) {
    this.role = {};
    this.createForm();
  }

  createForm() {
    this.saveForm = this.fb.group({
      Name: [this.role.Name || '', Validators.required],
      Description: [this.role.Description || '', Validators.required],
      Estatus: [this.role.Estatus || 0, Validators.required],
      Id: [this.role.Id || '', Validators.required]
    });
  }

  get(id: string) {

    if (id) {
      this.rolesService.getById(id).subscribe(res => {
        if (res.StatusCode === 200) {
          this.role = res.Result;
          delete this.role.Users;
          this.saveForm.setValue(this.role);
        } else {
          this.alertService.warning({
            title: res.error_description || 'No fue posible obtener los datos del rol'
          });
        }
      });
    } else {
      this.alertService.warning({
        title: 'No se encontro el un ID para el rol'
      });
    }

  }

  save() {

    this.rolesService.update(this.saveForm.value).subscribe(res => {
      if (res.StatusCode === 200) {
        this.alertService.success({
          title: 'Rol Actualizado exitosamente'
        });

        this.router.navigate(['/security/roles']);
      } else {
        this.alertService.warning({
          title: res.error_description || 'No fue posible actualizar el rol'
        });
      }
    });

  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.get(params['id']);
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  get Name() {
    return this.saveForm.get('Name');
  }

  get Description() {
    return this.saveForm.get('Description');
  }

  get Estatus() {
    return this.saveForm.get('Estatus');
  }
}



